import React, { Component } from 'react';
import Dock1 from './image/Web/Dock1.jpg'
import Dock2 from './image/Web/Dock2.jpg'
import Dock3 from './image/Web/Dock3.jpg'
import Cal1 from './image/Web/Cal1.jpg'
import Cal2 from './image/Web/Cal2.jpg'
import Cal3 from './image/Web/Cal3.jpg'
import Che1 from './image/Web/Che1.jpg'
import Che2 from './image/Web/Che2.jpg'
import Cup1 from './image/Web/Cup1.jpg'
import Cup2 from './image/Web/Cup2.jpg'
import Lin1 from './image/Web/Lin1.jpg'
import Lin2 from './image/Web/Lin2.jpg'
import Pla1 from './image/Web/Pla1.jpg'
import Cut from './image/Web/Cut.jpg'
import Sboard from './image/Sboard.jpg'
import pic3 from './image/card.jpg'

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Card, CardMedia, CardTitle, CardText} from 'material-ui/Card';

import './App.css';

class Cards extends Component {
  render() {
    return (
      <MuiThemeProvider>
      <div className="App">
	      <div className="CardStyle">
		      <Card zDepth={2}>
		      	<CardTitle title="手绘日历" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>
				    <CardMedia expandable={true}>
				      <div style={{width:'49%'}}>
				      <Card>
				      <CardMedia><img src={Cal1} alt=""/></CardMedia>
				      </Card>
				      </div>
				      <div style={{width:'49%'}}>
				      <CardMedia><img src={Cal2} alt=""/></CardMedia>
				      </div>
				      
				      <img src={Cal3} alt=""/>
				    </CardMedia>
				    <CardText expandable={true} actAsExpander={true} >
				      GYZ手绘校园日历，可加价订制底座刻字内容。此外还有手绘明信片单独销售，GYZ第三年手绘明信片，老品牌品质保障。
				    </CardText>

			  	</Card> 
	      </div>

	      <div className="CardStyle">
		      <Card zDepth={2}>
			    <CardTitle title="黑胡桃木手机座" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

			    <CardMedia expandable={true}>
			      <img src={Dock1} alt="" />
			      <img src={Dock2} alt=""/>
			      <img src={Dock3} alt=""/>
			    </CardMedia>
			    <CardText expandable={true} >
				      没错，就是好看，就是很有质感。
				</CardText>
			 	  </Card>
	      </div>
	    <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="一块奶酪" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={Che1} alt="" />
				      <img src={Che2} alt="" />
				    </CardMedia>
				    <CardText expandable={true} >
				      3D打印，奶酪形状的手机支架、笔座。
				    </CardText>
			  	</Card>
	      </div>
      	

	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="手机支撑片" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={pic3}  alt=""/>
				    </CardMedia>

			  	</Card>
	      </div>

	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="迷你托盘" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={Pla1}  alt=""/>
				    </CardMedia>

			  	</Card>
	      </div>

	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="年轮杯垫" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={Cup1} alt="" />
				      <img src={Cup2} alt="" />
				    </CardMedia>
				    <CardText expandable={true} >
				      精选年轮纹路制作而成的方形杯垫，环保节约。
				    </CardText>

			  	</Card>
	      </div>
	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="挂线器" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={Lin1} alt="" />
				      <img src={Lin2} alt="" />
				    </CardMedia>
				    <CardText expandable={true} >
				      可黏贴于墙面，悬挂充电线等。
				    </CardText>

			  	</Card>
	      </div>
	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="上海科技大学牌匾" subtitle="非卖品" actAsExpander={true} showExpandableButton={true} />

				    <CardMedia expandable={true}>
				      <img src={Sboard} alt="sboard" />
				    </CardMedia>
				    <CardText expandable={true} actAsExpander={true}>
				      木理坊招牌，独一无二仅此一块，非卖品。
				    </CardText>
			  	</Card>			  
	      </div>
	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="实木手工制作" subtitle="精心切割，打磨上蜡，还原光滑舒适的原木手感" />
				    <CardText >预定和购买详情请查看左上角菜单栏</CardText>
				    <CardMedia>
				      <img src={Cut} alt="" />
				    </CardMedia>

			  	</Card>
	      </div>

      </div>
      </MuiThemeProvider>
    );
  }
}

export default Cards;



